<template>
  <div id="works_page" class="works_page">
    <div id="title_part" class="title_part">
      <div id="title" class="title">{{ global.lang_pack.our_works }}</div>
      <div id="title_line" class="title_line"></div>
    </div>
    <div class="works_generally_intro">
      {{ global.lang_pack.works_generally_intro }}
    </div>

    <div class="works_list_part">
      <div class="works_row">
        <WorksCover
          v-if="config_list[0]"
          :id="id_list[0]"
          :cover_src="'/static/works/'+id_list[0]+'/cover.png'"
          :cover_title="config_list[0].cover_title"
          :tag_list="config_list[0].tag_list"
          :cover_intro="config_list[0].cover_intro"
          img_height=511
          img_width=400
        ></WorksCover>

        <WorksCover
          v-if="config_list[1]"
          :id="id_list[1]"
          :cover_src="'/static/works/'+id_list[1]+'/cover.png'"
          :cover_title="config_list[1].cover_title"
          :tag_list="config_list[1].tag_list"
          :cover_intro="config_list[1].cover_intro"
          img_height=280
          img_width=400
          style="height:450rem;"
        ></WorksCover>

        <WorksCover
          v-if="config_list[2]"
          :id="id_list[2]"
          :cover_src="'/static/works/'+id_list[2]+'/cover.png'"
          :cover_title="config_list[2].cover_title"
          :tag_list="config_list[2].tag_list"
          :cover_intro="config_list[2].cover_intro"
          img_height=511
          img_width=400
        ></WorksCover>
      </div>

      <div id="works_row" class="works_row">
        <WorksCover
          v-if="config_list[3]"
          :id="id_list[3]"
          :cover_src="'/static/works/'+id_list[3]+'/cover.png'"
          :cover_title="config_list[3].cover_title"
          :tag_list="config_list[3].tag_list"
          :cover_intro="config_list[3].cover_intro"
          img_height=280
          img_width=400
          style="height:450rem;"
        ></WorksCover>

        <WorksCover
          v-if="config_list[4]"
          :id="id_list[4]"
          :cover_src="'/static/works/'+id_list[4]+'/cover.png'"
          :cover_title="config_list[4].cover_title"
          :tag_list="config_list[4].tag_list"
          :cover_intro="config_list[4].cover_intro"
          img_height=511
          img_width=400
          style="margin-top: -230rem;"
        ></WorksCover>

        <WorksCover
          v-if="config_list[5]"
          :id="id_list[5]"
          :cover_src="'/static/works/'+id_list[5]+'/cover.png'"
          :cover_title="config_list[5].cover_title"
          :tag_list="config_list[5].tag_list"
          :cover_intro="config_list[5].cover_intro"
          img_height=280
          img_width=400
          style="height:450rem;"
        ></WorksCover>
      </div>

    </div>
    <Footer></Footer>
    <Menu></Menu>
    <LangTransfer></LangTransfer>
  </div>
</template>

<style lang="css" scoped>
div {
  font-family: HelveticaNeueLTProLtEx;
  color: #37383d;
}

::-webkit-scrollbar {
  display: none;
}

div.works_page {
  background: #f7f7f7;
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  display: flex;
  flex-direction: column;
  overflow-y: scroll;
}

div.title_part {
  margin-top: 100rem;
  margin-left: 300rem;
}

div.title {
  color: #251714;
  font-family: HelveticaNeueLTProEx;
  font-size: 100rem;
  text-transform: uppercase;
}

div.title_line {
  background: #efcd24;
  width: 450rem;
  height: 8rem;
  left: 625rem;
}

div.works_generally_intro {
  margin-top: 50rem;
  font-size: 25rem;
  padding-left: 300rem;
  padding-right: 300rem;
}

div.works_list_part {
  margin-top: 100rem;
  margin-left: 300rem;
  margin-right: 300rem;
}

div.works_row {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-top: 50rem;
}
</style>

<script>
import global from "../global";
import Menu from "../../components/menu/Menu";
import WorksCover from "../../components/works/cover/Cover";
import LangTransfer from "../../components/lang_transfer/LangTransfer"
import Footer from "../../components/footer/Footer"

//export default
export default {
  name: "Works",

  data() {
    return {
      global: global,
      config_list: [],
      id_list: ["ATLANTIS", "MGM_HOTEL", "BASIS_INTERNATIONAL", "DYT_HOTEL", "GRAND_HYATT", "CENTRAL_YOMA"],
    };
  },
  components: {
    Menu,
    WorksCover,
    Footer,
    LangTransfer
  },
  beforeCreate() {
    //set documentElement root size and language package
    global.refresh_windows_size();
    global.set_lang_pack();
  },
  created() {
  },
  mounted() {
    // mount the window size listen
    window.onresize = () => {
      return (() => {
        global.refresh_windows_size();
      })();
    };
    this.refresh_root_size();
    Menu.methods.change_logo_color("#251714", "#efcd24", 1000);
    this.get_covers();
  },
  destroyed() {
  },
  watch: {
    // watch the size change to refresh
    "global.width": function (val, oldVal) {
      this.refresh_root_size();
    },
  },
  methods: {
    refresh_root_size() {
      let static_piexl = 700;
      if (global.width < static_piexl) {
        document.documentElement.style.fontSize = static_piexl / 1920 + "px";
        return;
      }
      document.documentElement.style.fontSize = global.width / 1920 + "px";
    },
    get_covers() {
      let that = this;
      that.id_list.forEach((id) => {
        that.config_list.push({
          cover_title: require("../../../static/works/" +
            id +
            "/config.js").id.replace("_", " "),
          tag_list: require("../../../static/works/" + id + "/config.js")[
          "works_content_" + global.lang
            ].tag_list,
          cover_intro: require("../../../static/works/" + id + "/config.js")[
          "works_content_" + global.lang
            ].cover_intro,
        });
      });
    },
  },
};
</script>



