<template>
  <div :style="{width: img_width + 'rem'}" class="cover_box" @click="to_this_works" @mouseenter="show_intro" @mouseleave="hide_intro">
    <div
      :style="{ width: img_width+ 'rem', height: img_height+ 'rem'}"
      class="img_box"
    >
      <img
        :src="cover_src"
        :style="{ width: img_width+ 'rem', height: img_height+ 'rem' }"
        class="cover_img"
      />
      <div class="cover_mask" :style="{width: img_width+ 'rem', height: mask_height +'rem'}">
        <div class="cover_mask_text" id="cover_mask_text">{{cover_intro}}</div>
      </div>
    </div>



    <div id="cover_title" class="cover_title">{{ cover_title }}</div>

    <div id="tag_list" class="tag_list">
      <div v-for="(tag, index) of tag_list" id="tag" :key="tag" class="tag">{{
          tag
        }}{{ index === tag_list.length - 1 ? "" : ", " }}
      </div>
    </div>
  </div>
</template>


<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
div {
  font-family: HelveticaNeueLTProLtEx;
  color: #37383d;
}

div.img_box {
  overflow: hidden;
}

div.cover_mask{
  background: #efcd24;
  transform: translateY(-100%);
}

div.cover_mask_text{
  font-size: 20rem;
  padding: 10rem;
  word-break: break-word;
  word-wrap: break-word;
  text-align: justify;
  text-justify: distribute-all-lines;
  /*letter-spacing: 0.5px;*/
}

img.cover_img {
  object-fit: cover;
}

div.cover_title {
  padding-top: 30rem;
  color: #251714;
  font-family: HelveticaNeueLTProEx;
  font-size: 30rem;
  text-transform: uppercase;
  line-height: 1;
}

div.tag_list {
  padding-top: 10rem;
  display: flex;
  flex-direction: row;
}

div.tag {
  display: inline-block;
  font-size: 20rem;
  text-transform: uppercase;
  line-height: 1;
  white-space: pre-wrap;
}
</style>


<script>
import anime from "../../../../modules/anime";


export default {
  name: "WorksCover",
  props: [
    "id",
    "cover_name",
    "img_width",
    "img_height",
    "cover_src",
    "cover_title",
    "tag_list",
    "cover_intro"
  ],
  data() {
    return {
      mask_height: 10,
    };
  },
  beforeMount() {
  },
  mounted() {
    window.onresize = () => {
      return (() => {
        this.refresh_root_size();
      })();
    };
  },
  destroyed() {
  },
  methods: {
    refresh_root_size() {
      let static_piexl = 960;
      let width =
        document.documentElement.clientWidth || document.body.clientWidth;
      if (width < static_piexl) {
        document.documentElement.style.fontSize = static_piexl / 1920 + "px";
        return;
      }
      document.documentElement.style.fontSize =
        document.documentElement.clientWidth / 1920 + "px";
    },
    to_this_works() {
      let that = this;
      setTimeout(function () {
        that.$router.push("/Works/" + that.id);
      }, 0);
    },
    show_intro(){
      let that = this
      let img_height = this.img_height / 3
      anime({
        targets: that,
        mask_height: img_height,
        duration: 400,
        easing: "easeOutBack"
      });
    },
    hide_intro(){
      let that = this
      anime({
        targets: that,
        mask_height: 10,
        duration: 400,
        easing: "easeOutExpo"
      });
    }
  },
};
</script>



